.headers-container {
  margin: 0 auto;
  width: 80%;
  color: #fff;
  position: relative;
  height: 85vh;
}

.sub-header-container {
  font-size: 1.3rem;
  border-bottom: 1px solid #444;
}

.sub-headers-1 {
  display: flex;
  justify-items: space-between;
  align-items: center;
  gap: 1rem;
}

.sub-headers-2 {
  display: flex;
  justify-content: space-between;
  flex: 1;
  align-items: center;
  padding: 1rem 0.8rem;
  border-right: 1px solid #444;
}

.header-icon {
  color: #f26822;
  padding-right: 0.1rem;
  cursor: pointer;
}

/* .header-icon:hover {
  border-radius: 90%;
  background-color: #999;
  color: #fff;
} */

.main-headers-container {
  display: flex;
  font-size: 1.5rem;
  padding: 2rem 0.8rem;
  justify-content: space-between;
  gap: 5rem;
}
.main-headers-1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
}

.main-headers-2 {
  display: flex;
  align-items: center;
}
.main-headers {
  display: flex;
  gap: 3rem;
  list-style-type: none;
}
.main-headers .header {
  font-weight: 400;
  white-space: nowrap;
}
.main-headers .header:hover {
  color: #f26822;
  cursor: pointer;
}
.company-logo {
  height: 5.5rem;
}

.company-logo-container {
  flex-grow: 1;
}
